<template>
  <div class="page-container">
    <div class="home">
      <div class="hero-section">
        <div class="hero-content">
          <h1>ÂñµÊòü‰∫∫ÁöÑÁîüÊ¥ªËÆ∞ÂΩï</h1>
          <p>ËÆ∞ÂΩïÊØè‰∏Ä‰∏™Ê∏©ÊöñÁû¨Èó¥</p>
        </div>
        <div class="search-box">
          <input 
            type="text" 
            v-model="searchQuery" 
            placeholder="ÊêúÁ¥¢ËÆ∞ÂΩï..."
            class="search-input"
          >
          <button class="search-btn">
            <span class="icon">üîç</span>
          </button>
        </div>
      </div>

      <div class="pets-filter">
        <div class="pets-list">
          <div 
            v-for="pet in pets" 
            :key="pet.id" 
            class="pet-item"
            :class="{ active: selectedPetId === pet.id }"
            @click="selectPet(pet.id)"
          >
            <img :src="pet.avatar" :alt="pet.name" class="pet-avatar">
            <span class="pet-name">{{ pet.name }}</span>
          </div>
        </div>
      </div>

      <div class="type-filter">
        <div 
          v-for="type in recordTypes" 
          :key="type.value"
          class="type-tag"
          :class="{ active: selectedTypes.includes(type.value) }"
          @click="toggleType(type.value)"
        >
          <span class="type-icon">{{ type.icon }}</span>
          <span class="type-label">{{ type.label }}</span>
        </div>
      </div>

      <div class="timeline">
        <div v-for="record in filteredRecords" :key="record.id" class="timeline-item">
          <div class="timeline-date">{{ record.date }}</div>
          <div class="timeline-content">
            <div class="record-card" :class="record.type">
              <div class="record-header">
                <div class="pet-info">
                  <img :src="record.petAvatar" :alt="record.petName" class="pet-avatar">
                  <span class="pet-name">{{ record.petName }}</span>
                </div>
                <div class="record-type">
                  <span class="type-icon">{{ getTypeIcon(record.type) }}</span>
                  <span class="type-label">{{ getTypeLabel(record.type) }}</span>
                </div>
              </div>
              <div class="record-body">
                <h3 class="record-title">{{ record.title }}</h3>
                <p class="record-description">{{ record.description }}</p>
                <div v-if="record.nextDate" class="next-date">
                  <span class="icon">üìÖ</span>
                  <span>‰∏ãÊ¨°Êó•ÊúüÔºö{{ record.nextDate }}</span>
                </div>
              </div>
              <div class="record-actions">
                <button class="action-btn edit" @click="editRecord(record)">
                  <span class="icon">‚úèÔ∏è</span>
                  <span>ÁºñËæë</span>
                </button>
                <button class="action-btn delete" @click="deleteRecord(record)">
                  <span class="icon">üóëÔ∏è</span>
                  <span>Âà†Èô§</span>
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Home',
  data() {
    return {
      searchQuery: '',
      selectedPetId: null,
      selectedTypes: [],
      pets: [
        {
          id: 1,
          name: 'Â∞èÁÅ∞ÁÅ∞',
          avatar: require('@/assets/normal_cat.png')
        },
        {
          id: 2,
          name: 'Ëä±Ëä±',
          avatar: require('@/assets/cute_cat.png')
        }
      ],
      recordTypes: [
        { value: 'daily', label: 'Êó•Â∏∏ËÆ∞ÂΩï', icon: 'üìù' },
        { value: 'health', label: 'ÂÅ•Â∫∑ËÆ∞ÂΩï', icon: 'üíâ' },
        { value: 'grooming', label: 'Ê¥óÊä§ËÆ∞ÂΩï', icon: 'üõÅ' },
        { value: 'notes', label: 'ÈöèÊâãËÆ∞', icon: 'üì∏' }
      ],
      records: [
        {
          id: 1,
          type: 'health',
          date: '2024-03-20',
          petId: 1,
          petName: 'Â∞èÁÅ∞ÁÅ∞',
          petAvatar: require('@/assets/normal_cat.png'),
          title: 'Áå´‰∏âËÅîÁñ´Ëãó',
          description: 'Êé•Áßç‰∫ÜÁå´‰∏âËÅîÁñ´ËãóÔºåÂåªÁîüÂª∫ËÆÆËßÇÂØü30ÂàÜÈíüÔºåÊó†‰∏çËâØÂèçÂ∫î',
          nextDate: '2024-04-20'
        },
        {
          id: 2,
          type: 'daily',
          date: '2024-03-19',
          petId: 2,
          petName: 'Ëä±Ëä±',
          petAvatar: require('@/assets/cute_cat.png'),
          title: '‰ΩìÈáçËÆ∞ÂΩï',
          description: '‰ªäÊó•‰ΩìÈáçÔºö4.5kgÔºåËæÉ‰∏äÂë®Â¢ûÂä†0.2kg',
          nextDate: null
        }
      ]
    }
  },
  computed: {
    filteredRecords() {
      return this.records.filter(record => {
        const matchesPet = !this.selectedPetId || record.petId === this.selectedPetId
        const matchesType = this.selectedTypes.length === 0 || this.selectedTypes.includes(record.type)
        const matchesSearch = !this.searchQuery || 
          record.title.toLowerCase().includes(this.searchQuery.toLowerCase()) ||
          record.description.toLowerCase().includes(this.searchQuery.toLowerCase()) ||
          record.petName.toLowerCase().includes(this.searchQuery.toLowerCase())
        return matchesPet && matchesType && matchesSearch
      }).sort((a, b) => new Date(b.date) - new Date(a.date))
    }
  },
  methods: {
    selectPet(petId) {
      this.selectedPetId = this.selectedPetId === petId ? null : petId
    },
    toggleType(type) {
      const index = this.selectedTypes.indexOf(type)
      if (index === -1) {
        this.selectedTypes.push(type)
      } else {
        this.selectedTypes.splice(index, 1)
      }
    },
    getTypeIcon(type) {
      const typeInfo = this.recordTypes.find(t => t.value === type)
      return typeInfo ? typeInfo.icon : 'üìù'
    },
    getTypeLabel(type) {
      const typeInfo = this.recordTypes.find(t => t.value === type)
      return typeInfo ? typeInfo.label : 'ÂÖ∂‰ªñ'
    },
    editRecord(record) {
      // Ë∑≥ËΩ¨Âà∞ÂØπÂ∫îÁöÑËÆ∞ÂΩïÈ°µÈù¢ËøõË°åÁºñËæë
      this.$router.push(`/${record.type}`)
    },
    deleteRecord(record) {
      if (confirm('Á°ÆËÆ§Âà†Èô§ËØ•ËÆ∞ÂΩïÂêóÔºü')) {
        this.records = this.records.filter(r => r.id !== record.id)
      }
    }
  }
}
</script>

<style scoped>
