  max-width: 500px;
}

.search-input {
  flex: 1;
  padding: 12px 20px;
  border: 2px solid #eee;
  border-radius: 25px;
  font-size: 16px;
  transition: all 0.3s ease;
}

.search-input:focus {
  border-color: #ff6b9c;
  outline: none;
  box-shadow: 0 0 0 3px rgba(255, 107, 156, 0.1);
}

.search-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 45px;
  height: 45px;
  border: none;
  border-radius: 50%;
  background: #ff6b9c;
  color: white;
  cursor: pointer;
  transition: all 0.3s ease;
}

.search-btn:hover {
  background: #ff85c2;
  transform: translateY(-2px);
}


.timeline {
  position: relative;
  padding-left: 30px;
}

.timeline::before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  width: 2px;
  background: #eee;
}

.timeline-item {
  position: relative;
  margin-bottom: 40px;
}

.timeline-item::before {
  content: '';
  position: absolute;
  left: -34px;
  top: 0;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: #ff6b9c;
  border: 2px solid white;
  box-shadow: 0 0 0 2px #ff6b9c;
}

.timeline-date {
  position: absolute;
  left: -120px;
  top: -5px;
  font-size: 14px;
  color: #666;
}

.timeline-content {
  background: white;
  border-radius: 15px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.05);
  overflow: hidden;
  transition: all 0.3s ease;
}

